import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { JournalVoucherFormRoutingModule } from './journal-voucher-form-routing.module';
import { JournalVoucherFormComponent } from './journal-voucher-form.component';
import { JournalVoucherFormAddComponent } from './journal-voucher-form-add/journal-voucher-form-add.component';
import { JournalVoucherFormEditComponent } from './journal-voucher-form-edit/journal-voucher-form-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    JournalVoucherFormRoutingModule
  ],
  entryComponents:[
    JournalVoucherFormAddComponent,
    JournalVoucherFormEditComponent
  ],
  declarations: [
    JournalVoucherFormComponent,
    JournalVoucherFormAddComponent,
    JournalVoucherFormEditComponent
],
schemas: [ NO_ERRORS_SCHEMA ]

})
export class JournalVoucherFormModule { }


