import { Component, OnInit,Input, Output, EventEmitter, ViewChild, Inject, ChangeDetectorRef } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { JournalVoucherFormService,AccountTransactionService,JournalVoucherForm ,ErrorHandleService, CurrentUserService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-journal-voucher-form-edit',
  templateUrl: './journal-voucher-form-edit.component.html',
  styleUrls: ['./journal-voucher-form-edit.component.scss']
})
export class JournalVoucherFormEditComponent implements OnInit {
  @ViewChild('jvfEditGrid') jvfEditGrid: jqxGridComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('myComboBox2') myComboBox2: jqxComboBoxComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @Input('JVFEditData') JVFEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  jvfForm: FormGroup;
  apiUrl: string;
  accHeadItemSource: any;
  accHeadItemAdapter: any;
  itemLoader:boolean = false;
  costCenterSource: any;
  costCenterAdapter: any;
  valueTotal:any;
  EditData: JournalVoucherForm;
  accArray: Array<any> = [];

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private ehs: ErrorHandleService,
    private jvfs: JournalVoucherFormService,
    private cdr: ChangeDetectorRef,
    private ats: AccountTransactionService,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.createForm();
  }

   ngOnInit() {  
     console.info(this.JVFEditData);
  this.EditData=new JournalVoucherForm(this.JVFEditData);
  this.jvfForm.setValue(this.EditData);
     this.accHeadItemSource = {
      datatype: "json",
      contentType: "application/json; charset=utf-8",
      datafields: [
        { name: 'id' },
        { name: 'account_head_id' },
        { name: 'code' }
      ],
      data: this.userData,
      url: this.apiUrl + 'account_head?limit=all'
    };
  
    this.accHeadItemAdapter = new jqx.dataAdapter(this.accHeadItemSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (let i = 0; i < tData.length; i++) {
          tData[i]['code'] = tData[i]['code'] + ' - ' + tData[i]['name'];
          tData[i]['account_head_id'] = tData[i]['id'];

        }
        this.itemLoader = false;
        return tData;
      },
      loadError: (jqXHR, status, error) => {
        this.itemLoader = false;
      }
    });
    this.costCenterSource =
    {
      datatype: "json",
      contentType: "application/json; charset=utf-8",
      datafields: [
        { name: 'id' },
        { name: 'name' },
        { name: 'cost_center_id'}

      ],
      data: this.userData,
      async: false,
      url: this.apiUrl + 'costcenter?limit=all'
    };

    this.costCenterAdapter = new jqx.dataAdapter(this.costCenterSource, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        this.itemLoader = false;
        return tData;
      },
      loadError: (jqXHR, status, error) => {
        this.itemLoader = false;
      }
    });
  }
   ngAfterViewInit(){
      this.source.localdata = this.JVFEditData['account_transaction_splits'];
      this.jvfEditGrid.updatebounddata("cells");
      this.cdr.detectChanges();
   }

  createForm() {
    this.jvfForm = this.fb.group({
      'date': [null, Validators.required],
      'vouchar_no': [null, Validators.required],
    });
  }
  addNewItem() {
    let datarow = this.generaterow();
    this.jvfEditGrid.addrow(null, datarow);
  }
  generaterow(): any {
    let row = {};
    row['account_head_id'] = '';
    row['cost_center_id'] = '';
    row['value'] = '';
    row['remarks'] = '';
    row['reference'] = '';
    return row;
  }
  source: any =
  {
    datatype: 'json',
    datafields:
    [
      { name: 'account_head_id', type: 'string',map:'account_head>id' },
      { name: 'account_head_name', type: 'string',map:'account_head>name' },
      { name: 'cr_dr', type: 'string' },
      { name: 'cost_center_id', type: 'string',map:'cost_center>id' },
      { name: 'cost_center_name', type: 'string',map:'cost_center>name' },
      { name: 'cr_value', type: 'string',map:'value>cr_value' },
      { name: 'dr_value', type: 'string',map:'value>dr_value' },
      { name: 'value', type: 'string' },
      { name: 'remarks', type: 'string' },
      { name: 'reference', type: 'string' },
    ],
    updaterow: (rowid: any, rowdata: any, commit: any): void => {
      commit(true);
    },
    localdata: []
  };

  dataAdapter: any = new jqx.dataAdapter(this.source);

  crDrAdapter: any[] = [
    { cr_dr: 'cr', label: 'CR' },
    { cr_dr: 'dr', label: 'DR' },
  ];

  columns: any[] =
  [
    {
      text: 'Accound Head', datafield: 'account_head_id', displayfield: 'account_head_name', editable: true, columntype: 'combobox',
      createeditor: (row: number, column: any, editor: any): void => {
        this.itemLoader = true;
        editor.jqxComboBox({
          source: this.accHeadItemAdapter,
          displayMember: "code",
          valueMember: "id",
          searchMode: "containsignorecase"
        });
      },
    },
    {
      text: 'Dr', datafield: 'dr_value', columntype: 'textbox', width: 100,
      cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
        if (newvalue != oldvalue) {
          let currentRow = this.jvfEditGrid.getrowdata(row).boundindex;
          this.jvfEditGrid.setcellvalue(currentRow,'cr_value',0);
        }
      },
      aggregates: [{
        'Total': (aggregatedValue: number, currentValue: number, column, record): number => {
          let total = parseFloat(record['dr_value']);
            return aggregatedValue + total;
        }
      }], aggregatesrenderer: (aggregates: any): string => {
        let value = aggregates['Total'];
        this.valueTotal = value;
        if (!value) {
          value = 0;
          this.valueTotal = 0;
        }
        let renderstring = '<div style="float: left; margin: 4px; overflow: hidden;">' + "Total" + ': ' + value + '</div>';
        return renderstring;
      }
    }
    ,
    {
      text: 'Cr', datafield: 'cr_value', columntype: 'textbox', width: 100,
      cellvaluechanging: (row: number, datafield: string, columntype: any, oldvalue: any, newvalue: any): void => {
        if (newvalue != oldvalue) {
          let currentRow = this.jvfEditGrid.getrowdata(row).boundindex;
          this.jvfEditGrid.setcellvalue(currentRow,'dr_value',0);
        }
      },
      aggregates: [{
        'Total': (aggregatedValue: number, currentValue: number, column, record): number => {
          let total = parseFloat(record['cr_value']);
            return aggregatedValue + total;
        }
      }], aggregatesrenderer: (aggregates: any): string => {
        let value = aggregates['Total'];
        this.valueTotal = value;
        if (!value) {
          value = 0;
          this.valueTotal = 0;
        }
        let renderstring = '<div style="float: left; margin: 4px; overflow: hidden;">' + "Total" + ': ' + value + '</div>';
        return renderstring;
      },
    },
    {
      text: 'Cost Center', datafield: 'cost_center_id', displayfield: 'cost_center_name', columntype: 'combobox',
      createeditor: (row: number, column: any, editor: any): void => {
        this.itemLoader = true;
        editor.jqxComboBox({
          source: this.costCenterAdapter,
          displayMember: "name",
          valueMember: "id",
          searchMode: "containsignorecase"
        });
      }
    },

    { text: 'Remarks', datafield: 'remarks', columntype: 'textbox', width: 140 },
    {
      text: 'Reference', datafield: 'reference', columntype: 'textbox', width: 150,

    },

    {
      text: 'Actions', datafield: 'Delete', columntype: 'button', width: 70,
      cellsrenderer: (): string => {
        return 'Delete';
      },
      buttonclick: (row: number): void => {
        let rowid = this.jvfEditGrid.getrowid(row)
        this.jvfEditGrid.deleterow(rowid);
      }
    }
  ];
  saveBtn(post) {
    let jvfgridData = this.jvfEditGrid.getrows();
    for(let i = 0; i < jvfgridData.length; i++){
      jvfgridData[i]['value'] = (jvfgridData[i]['cr_dr'] == 'cr') ? -Math.abs(jvfgridData[i]['value']) : Math.abs(jvfgridData[i]['value']);
    }
    post['accountTransactionSplits'] = jvfgridData;
    post['vouchar_type'] = 'JV';
    this.myValidator.validate(document.getElementById('jvfForm'));
    if(post['accountTransactionSplits'].length > 0){
      this.jqxLoader.open();
      this.ats.update(this.JVFEditData.id,post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }   
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
  [
   

  ];

}



