import { Component, OnInit, ViewChild, Inject, ComponentFactoryResolver, ElementRef, ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { JournalVoucherFormService,AccountTransactionService, QuickaccessComponent, FileExportService, CurrentUserService} from '../../../shared';
import {JournalVoucherFormAddComponent } from './journal-voucher-form-add/journal-voucher-form-add.component';
import {JournalVoucherFormEditComponent } from './journal-voucher-form-edit/journal-voucher-form-edit.component';
import * as pdfMake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { forEach } from '@angular/router/src/utils/collection';


@Component({
  selector: 'app-journal-voucher-form',
  templateUrl: './journal-voucher-form.component.html',
  styleUrls: ['./journal-voucher-form.component.scss']
})
export class JournalVoucherFormComponent implements OnInit {
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('errNotification') errNotification: jqxNotificationComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myDetailGrid') myDetailGrid: jqxGridComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('myDetailWindow') myDetailWindow: jqxWindowComponent;
  @ViewChild('Insert', { read: ViewContainerRef }) Insert: ViewContainerRef;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;

  editrow: number = -1;
  approveReqForm: FormGroup;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  detailDataAdapter:any;
  detailColumns: any[];
  columns: any[];
  tableDatas:Array<any> = [];
  userData: any = {};
  userDataRole: any = {};
  selectedOrderList: Array<any> = [];

  constructor(
    private jvfs: JournalVoucherFormService,
    private componentFactoryResolver: ComponentFactoryResolver,
    @Inject('API_URL') apiUrl: string,
    private fileExport: FileExportService,
    private ats: AccountTransactionService,
    private fb: FormBuilder,
    private cus: CurrentUserService
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.userDataRole = this.cus.getRole();
    this.getJVForm();
    this.createForm();
  }

  ngOnInit() {
    this.createQuickAccess();
  }
  ngAfterViewInit() {
    console.info(this.userDataRole);
     setTimeout(() => {
       this.approveReqForm.controls['approve_by'].setValue(this.userDataRole['name']);
       this.approveReqForm.get('approve_by').markAsTouched();
     }, 100);
 }
  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  createForm() {
    this.approveReqForm = this.fb.group({
      'approve_date': [null, Validators.required],
      'approve_by': [null, Validators.required]
    });
  }
  
  getJVForm() {
    let myUrl = this.apiUrl + 'account_transaction?vouchar_type=JV&required=VOUCHAR_FORM';
    this.source =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'number' },
          { name: 'enter_date', type: 'string' },
          { name: 'vouchar_no', type: 'number' },
          { name: 'enter_by', type: 'number' },
          { name: 'total_amount', type: 'number' },
          { name: 'remarks', type: 'string' },
          { name:'vouchar_details'}
        ],
        pagesize: 20,
         url: myUrl,
         data: this.userData,
        // totalrecords: 100,
        filter: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('filter');
        },
        sort: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('sort');
        },
        beforeprocessing: (data) => {

        }
      }

    this.dataAdapter = new jqx.dataAdapter(this.source, {
      formatData: (data) => {
        data['page'] = data['pagenum'] + 1;
        data['limit'] = data['pagesize'];
        return data;
      }, downloadComplete: (data, status, xhr) => {
        this.source.totalrecords = data.data.total;
        let tData = data.data.data;
        console.log(tData);
        return tData;
      } , loadError: (jqXHR, status, error) => {
        let messageDiv: any = document.getElementById('listingMessage');
        messageDiv.innerText = error;
        this.winNotification.open();
        this.jqxLoader.close();
      }
    });

    this.columns =
      [
        {
          text: 'S.N', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Date', datafield: 'enter_date', filtertype: 'date', },
        { text: 'Voucher No', datafield: 'vouchar_no', columntype: 'textbox', filtercondition: 'starts_with' },
        { text: 'Enter By', datafield: 'enter_by', columntype: 'textbox', filtercondition: 'starts_with' },
        { text: 'Total Amount', datafield: 'total_amount', columntype: 'textbox', filtercondition: 'starts_with' },
        // {
        //   text: 'Actions', datafield: 'Edit', columntype: 'button',sortable:false,filterable:false, width: 70,
        //   cellsrenderer: (): string => {
        //     return 'Edit';
        //   },
        //   buttonclick: (row: number): void => {
        //     this.editrow = row;
        //     let dataRecord = this.myGrid.getrowdata(this.editrow);
        //     this.myWindow.draggable(true);
        //     this.myWindow.title('Edit');
        //     this.myWindow.open();
        //     const componentFactory = this.componentFactoryResolver.resolveComponentFactory(JournalVoucherFormEditComponent);
        //     this.Insert.clear();
        //     const dynamic_Component = this.Insert.createComponent(componentFactory);
        //     (<JournalVoucherFormEditComponent>dynamic_Component.instance).JVFEditData = dataRecord;
        //     (<JournalVoucherFormEditComponent>dynamic_Component.instance).UpdatedData.subscribe(result => {
        //       if (result.result == true) {
        //         let messageDiv = document.getElementById('message');
        //         messageDiv.innerText = result['message'];
        //         this.msgNotification.open();
        //         this.myWindow.hide();
        //         this.myGrid.updatebounddata();
        //         dynamic_Component.destroy();
        //       }else{
        //         let messageDiv = document.getElementById('message');
        //         messageDiv.innerText = result['message'];
        //         this.msgNotification.open();
               
        //       }
        //     });

        //     (<JournalVoucherFormEditComponent>dynamic_Component.instance).cancelData.subscribe(result => {
        //       if (result == true) {
        //         this.myWindow.hide();
        //         dynamic_Component.destroy();
        //       }
        //     });
        //   }
        // },
        {
          text: 'Actions', datafield: 'View', columntype: 'button',sortable:false,filterable:false, width: 150,
          cellsrenderer: (): string => {
            return 'View Detail';
          },
          buttonclick: (row: number): void => {
            this.tableDatas = [];
            this.editrow = row;
            let dataRecord = this.myGrid.getrowdata(this.editrow);
            this.tableDatas = dataRecord['vouchar_details'];
            console.info(this.tableDatas); 
            this.myDetailWindow.draggable(true);
            this.myDetailWindow.title('Journal Detail');
            this.myDetailWindow.open();
           
          }
        }
      ];
  }

  rendergridrows = (params: any): any[] => {
    return params.data;
  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let buttonContainer1 = document.createElement('div');
    let buttonContainer2 = document.createElement('div');
    let buttonContainer3 = document.createElement('div');
    let buttonContainer4 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');
    let buttonContainer6 = document.createElement('div');

    buttonContainer1.id = 'buttonContainer1';
    buttonContainer2.id = 'buttonContainer2';
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer4.id = 'buttonContainer4';
    buttonContainer5.id = 'buttonContainer5';
    buttonContainer6.id = 'buttonContainer6';

    buttonContainer1.style.cssText = 'float: left';
    buttonContainer2.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer4.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer5.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer6.style.cssText = 'float: left; margin-left: 5px';

    container.appendChild(buttonContainer1);
    container.appendChild(buttonContainer3);
    container.appendChild(buttonContainer2);
    container.appendChild(buttonContainer4);
    container.appendChild(buttonContainer5);
    container.appendChild(buttonContainer6);
    toolbar[0].appendChild(container);

    let addRowButton = jqwidgets.createInstance('#buttonContainer1', 'jqxButton', { width: 105, value: 'Add New Row', theme: 'energyblue'  });
    let deleteRowButton = jqwidgets.createInstance('#buttonContainer3', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme: 'energyblue'  });
     let resetRowButton = jqwidgets.createInstance('#buttonContainer2', 'jqxButton', { width: 150, value: 'Reset Search Data', theme: 'energyblue' });
   

    resetRowButton.addEventHandler('click', () => {
      this.myGrid.clearfilters();
    });

    addRowButton.addEventHandler('click', () => {
      this.myWindow.draggable(true);
      this.myWindow.title('Add Journal Voucher Form');
      this.myWindow.open();
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(JournalVoucherFormAddComponent);
      this.Insert.clear();
      const dynamic_Component = this.Insert.createComponent(componentFactory);
      (<JournalVoucherFormAddComponent>dynamic_Component.instance).AddedData.subscribe(result => {
        if (result.result == true) {
          let messageDiv = document.getElementById('message');
          messageDiv.innerText = result['message'];
          this.msgNotification.open();
          this.myWindow.hide();
          this.myGrid.updatebounddata();
          dynamic_Component.destroy();
        }else{
          let messageDiv = document.getElementById('message');
          messageDiv.innerText = result['message'];
          this.msgNotification.open();
         
        }
      });

      (<JournalVoucherFormAddComponent>dynamic_Component.instance).cancelData.subscribe(result => {
        if (result == true) {
          this.myWindow.hide();
          dynamic_Component.destroy();
        }
      });

    })

    deleteRowButton.addEventHandler('click', () => {
      let id = this.myGrid.getselectedrowindexes();
        let ids = [];
        let rowscount = this.myGrid.getdatainformation().rowscount;
        for(let i=0; i<id.length; i++){
          let dataRecord = this.myGrid.getrowdata(Number(id[i]));
          ids.push(dataRecord.id);
        }
        if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
          if (confirm("Are you sure? You Want to delete")) {
             this.jqxLoader.open();            
            this.ats.destroy('['+ids+']').subscribe(data => {
              let messageDiv = document.getElementById('message');
              messageDiv.innerText = data['message'];
              this.msgNotification.open();
              this.jqxLoader.close();
              this.myGrid.updatebounddata('cells');   
            })
          }
        }else{
             let messageDiv = document.getElementById('message');
              messageDiv.innerText = 'Please select some item to delete';
              this.msgNotification.open();
        }
      })

  
    }

    rowChange(event: any) {
      let dataRecord = this.myGrid.getrowdata(event.args.rowindex);
      if(dataRecord){
        this.selectedOrderList.push(dataRecord['id']);
      }
    }
  
    rowUnChange(event: any) {
      let dataRecord = this.myGrid.getrowdata(event.args.rowindex);
      if(dataRecord){
        let index = this.selectedOrderList.findIndex(x => x == dataRecord['id']);
        if (index > -1) {
          this.selectedOrderList.splice(index, 1);
        }
      }
    }
    
    approve(formData) {
      this.jqxLoader.open();
      formData['ids'] = this.selectedOrderList;
      formData['status'] = 'Approve';
      formData['multiple_approve'] = true;
      if (formData['ids'].length > 0) {
        this.ats.storeAprove(formData['ids'],formData).subscribe(
          result => {
            if (result['message']) {
              this.myGrid.clearselection();
              this.myGrid.updatebounddata();
              let messageDiv: any = document.getElementById('message');
              messageDiv.innerText = result['message'];
              this.msgNotification.open();
            }
            this.jqxLoader.close();
            if (result['error']) {
              this.myGrid.clearselection();
              this.myGrid.updatebounddata();
              let messageDiv: any = document.getElementById('error');
              messageDiv.innerText = result['error']['message'];
              this.errNotification.open();
            }
          },
          error => {
            this.jqxLoader.close();
            console.info(error);
          }
        );
      } else {
        this.jqxLoader.close();
        let messageDiv: any = document.getElementById('error');
        messageDiv.innerText = "Please Select at least one item";
        this.errNotification.open();
      }
    }
  
    reject(formData) {
      formData['ids'] = this.selectedOrderList;
      formData['status'] = 'Reject';
      formData['multiple_approve'] = true;
      if (formData['ids'].length > 0) {
        this.ats.storeAprove(formData['ids'],formData).subscribe(
          result => {
            if (result['message']) {
              this.myGrid.clearselection();
              this.myGrid.updatebounddata();
              let messageDiv: any = document.getElementById('message');
              messageDiv.innerText = result['message'];
              this.msgNotification.open();
            }
            this.jqxLoader.close();
            if (result['error']) {
              this.myGrid.clearselection();
              this.myGrid.updatebounddata();
              let messageDiv: any = document.getElementById('error');
              messageDiv.innerText = result['error']['message'];
              this.errNotification.open();
            }
          },
          error => {
            this.jqxLoader.close();
            console.info(error);
          }
        );
      } else {
        this.jqxLoader.close();
        let messageDiv: any = document.getElementById('error');
        messageDiv.innerText = "Please Select at least one item";
        this.errNotification.open();
      }
    }

}



